.card{
    background-image: linear-gradient(rgba(245, 51, 3, 0.863),rgba(253, 80, 0, 0.76));
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 5px;
    border-radius: 15px;
    margin: 2px;
    width: 7.5em;
    color:whitesmoke;
    font-size: large;
    font-weight: 500;
}